"use client";

import { useEffect, useState } from "react";
import QRCode from "qrcode";
import { Card, CardHeader, CardTitle, CardContent } from "@/components/ui/card";
import { Button } from "@/components/ui/button";

export default function ScannerPage({ params }) {
  const { shopId } = params;
  const [qr, setQr] = useState("");

  useEffect(() => {
    // Generate the URL that customers will scan
    const url = `${process.env.NEXT_PUBLIC_APP_URL}/claim?shopId=${shopId}`;

    // Generate QR → base64 image
    QRCode.toDataURL(url, { width: 500 }).then(setQr);
  }, [shopId]);


  /** ⬇ Download QR as PNG */
  const handleDownload = () => {
    const a = document.createElement("a");
    a.href = qr;
    a.download = `scanner-${shopId}.png`;
    a.click();
  };

  /** ⬇ Print QR */
  const handlePrint = () => {
    const win = window.open("");
    win.document.write(`<img src="${qr}" style="width:300px;" />`);
    win.print();
  };


  return (
    <div className="flex justify-center py-10">
      <Card className="w-[400px]">
        <CardHeader>
          <CardTitle className="text-center">
            Your Scanner QR Code
          </CardTitle>
        </CardHeader>

        <CardContent className="flex flex-col items-center gap-4">
          
          {qr && (
            <img
              src={qr}
              alt="Shop QR Code"
              className="w-64 h-64 rounded-md border"
            />
          )}

          <Button onClick={handleDownload} className="w-full">
            Download QR
          </Button>

          <Button onClick={handlePrint} variant="secondary" className="w-full">
            Print QR
          </Button>

          <p className="text-sm text-center text-muted-foreground mt-3">
            Share or print this QR and place it at your shop counter.  
            Customers will scan it to claim stamps using payment screenshots.
          </p>

        </CardContent>
      </Card>
    </div>
  );
}
